<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/ujiprodukjadi" link>Back</v-btn>

        <template id="template">
            <div align="center">
                <table border="1" cellspacing="0" cellpadding="0" width="662" class="text-center">
                    <tbody>
                        <tr>
                            <td width="260" colspan="3" valign="bottom" class="text-center">
                                <img class="mt-3" width="215" height="38" src="../../assets/logoNiagara1.png" />
                            </td>
                            <td width="389" colspan="5" rowspan="2">
                                <strong>
                                    LAPORAN PENGUJIAN PRODUK JADI 
                                </strong>
                            </td>
                        </tr>
                        <tr>
                            <td width="260" colspan="3">
                                <strong>
                                    BAGIAN PENGAWASAN MUTU
                                </strong>
                            </td>
                        </tr>
                        <tr>
                            <td width="90" nowrap="">
                                <strong>
                                    Tgl
                                </strong>
                            </td>
                            <td width="151" nowrap="">
                                <strong>
                                    Nama Produk
                                </strong>
                            </td>
                            <td width="114" nowrap="">
                                <strong>
                                    Kode Produk
                                </strong>
                            </td>
                            <!-- <td width="91" nowrap="">
                                <strong>
                                    Pemasok
                                </strong>
                            </td> -->
                            <td width="104" nowrap="">
                                <strong>
                                    No. Bets
                                </strong>
                            </td>
                            <td width="110" nowrap="">
                                <strong>
                                    No.Uji
                                </strong>
                            </td>
                            <td width="59" nowrap="">
                                <strong>
                                    Status
                                </strong>
                            </td>
                            <td width="55" nowrap="">
                                <strong>
                                    Paraf
                                </strong>
                            </td>
                        </tr>
                        <tr v-for="item in laporanujir" :key='item["No Uji"]'>
                            <td width="50" nowrap="" valign="bottom">
                                {{ item["Tanggal Pengujian"].substring(0,10) }}
                            </td>
                            <td width="126" nowrap="" valign="bottom">
                                {{ item["Nama Produk"] }}
                            </td>
                            <td width="84" nowrap="" valign="bottom">
                                {{ item["Kode Produk"] }}
                            </td>
                            <td width="89" nowrap="" valign="bottom">
                                {{ item["No Bets"] }}
                            </td>
                            <td width="84" nowrap="" valign="bottom">
                                {{ item["No Uji"] }}
                            </td>
                            <td width="59" nowrap="" valign="bottom">
                                {{ item["Kesimpulan"] }}
                            </td>
                            <td width="66" nowrap="" valign="bottom">
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </template>

    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                laporanujir: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'tabelujiprodukjadicpkbs'
                this.$http.get(uri).then(response => {
                    this.laporanujir = response.data
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
    }

    #table,
    #th,
    #td {
        border: 2px solid black;
        border-collapse: collapse;
    }
</style>